import { useState } from "react"

const CargandoProductos = () => {
    let [mensaje,setMensaje] = useState("Cargando productos...");

    setTimeout(() => {
        setMensaje("Por ahora no hay productos vuelve ms tarde.")
    }, 7000);

    return(
        <h3 style={{background:"linear-gradient(to right,#807e7e,#78a478)",padding:"85px",fontSize:"22px",textAlign:"center",height:"200px",position:"absolute",width:"100%",display:"inline-block"}}>{mensaje}</h3>
    )
}
export default CargandoProductos;