import { useEffect } from "react";
import "./pizarra.css"

const Pizarra = () =>{
    useEffect(()=>{
        let canvas = document.getElementById("canvas");
let ctx = canvas.getContext("2d");

let initialX,initialY;

const dibujar = (cursorX,cursorY) => {
    ctx.beginPath();
    ctx.moveTo(initialX,initialY);
    ctx.lineCap = "round";
    ctx.lineJoin = "round";
    ctx.lineTo(cursorX,cursorY);
    ctx.stroke();

    initialX = cursorX;
    initialY = cursorY;
}

function tamanoInicial(){
    let inicio = {
        target:{
            value:7
        }
    }
    cambiarRango(inicio);
}

const manejarSombra = () =>{
    if(sombra.checked){
        cambiarRangoSombra({target:{value:rangoSombra}});
    }else{
        ctx.shadowBlur = false;
    }
}
const sombra = document.getElementById("sombra");
sombra.addEventListener("change",manejarSombra)

const cambiarColor = (e) => ctx.strokeStyle = e.target.id;

const mouseDown = (e) => {
    initialX = e.offsetX;
    initialY = e.offsetY;
    dibujar(initialX,initialY);
    canvas.addEventListener("mousemove", moveMoving)
    // console.log("X: " + initialX)
    // console.log("Y: " + initialY)

}
const moveMoving = (e) => dibujar(e.offsetX,e.offsetY);
const mouseUp = () => canvas.removeEventListener("mousemove",moveMoving);

canvas.addEventListener("mousedown", mouseDown);
canvas.addEventListener("mouseup", mouseUp);

const cambiarRango = (e) => {
    if(e.target.value === 0){
        e.target.value = 1;
        ctx.lineWidth = e.target.value;
        contadorLinea.textContent = `Tamao de Linea: ${e.target.value}`;
    }else{
        ctx.lineWidth = e.target.value;
        contadorLinea.textContent = `Tamao de Linea: ${e.target.value}`;
    }
}
var rangoSombra;

const cambiarRangoSombra = (e) => {
    contadorSombra.textContent = `Tamao de Sombra: ${e.target.value}`;
    rangoSombra = e.target.value;
    if(sombra.checked) ctx.shadowBlur = e.target.value;
    else{
        ctx.shadowBlur = false;
        contadorSombra.textContent = `Tamao de Sombra: ${e.target.value}`;
    }
}
const contadorLinea = document.getElementById("contadorLinea");
const rangeLinea = document.getElementById("rangeLinea");
const contadorSombra = document.getElementById("contadorSombra");
const rangeSombra = document.getElementById("rangeSombra");
const colors = document.getElementsByClassName("color");
const sombras = document.getElementsByClassName("colorSombra");
rangeLinea.value = 7;
rangeLinea.addEventListener("change",cambiarRango);
rangeSombra.value = 2;
rangeSombra.addEventListener("change",cambiarRangoSombra)

for (let i = 0; i < colors.length; i++){
    colors[i].addEventListener("click", cambiarColor)
}

const colorSombra = ({target}) =>{
    if (sombra.checked) ctx.shadowColor = target.id;
    else ctx.shadowColor = target.id;
}

for (let i = 0; i < sombras.length; i++){
    sombras[i].addEventListener("click", colorSombra)
}

tamanoInicial()
colorSombra({target:{id:"black"}})
cambiarRangoSombra({target:{value:2}})
manejarSombra()
    },[])
    return (
    <div className="contenedor-pizarra">
        <div className="parteIzquierda" style={{padding: "0px"}}>
            <div>
                <label id="labelSombra" style={{color: "white",textTransform:"uppercase"}} htmlFor="sombra">Sombra</label>
                <input type="checkbox" id="sombra"/>
            </div>
            <div className="paletaColoresSombra">
                <div className="colorSombra" title="---" id="black" style={{background:"rgb(0, 0, 0)"}}></div>
                <div className="colorSombra" title="Blanco" id="white" style={{background: "rgb(255, 255, 255)"}}></div>
                <div className="colorSombra" title="Gris" id="grey" style={{background: "grey"}}></div>
                <div className="colorSombra" title="Rosado" id="pink" style={{background: "pink"}}></div>
                <div className="colorSombra" title="Agua" id="aqua" style={{background: "aqua"}}></div>
                <div className="colorSombra" title="Salmn" id="salmon" style={{background: "salmon"}}></div>
                <div className="colorSombra" title="Violeta" id="violet" style={{background: "violet"}}></div>
                <div className="colorSombra" title="Tomate" id="tomato" style={{background: "tomato"}}></div>
                <div className="colorSombra" title="---" id="springgreen" style={{background:"springgreen"}}></div>
                <div className="colorSombra" title="Amarillo" id="yellow" style={{background: "yellow"}}></div>
                <div className="colorSombra" title="---" id="olivedrab" style={{background: "olivedrab"}}></div>
                <div className="colorSombra" title="Chocolate" id="chocolate" style={{background:"chocolate"}}></div>
                <div className="colorSombra" title="Verde" id="green" style={{background: "green"}}></div>
                <div className="colorSombra" title="Rojo" id="red" style={{background: "red"}}></div>
                <div className="colorSombra" title="---" id="crimson" style={{background: "crimson"}}></div>
                <div className="colorSombra" title="Marrn" id="brown" style={{background: "brown"}}></div>
                <div className="colorSombra" title="Marrn oscuro" id="maroon" style={{background: "maroon"}}></div>
                <div className="colorSombra" title="---" id="slateblue" style={{background: "slateblue"}}></div>
                <div className="colorSombra" title="Purpura" id="purple" style={{background: "purple"}}></div>
                <div className="colorSombra" title="Azul" id="blue" style={{background: "blue"}}></div>
            </div>
            <div className="ranges" style={{border:"2px solid rgb(31, 29, 29)",padding: "20px 20px",width: "250px",boxSizing: "border-box",color:"white"}}>
            
                <div className="contador" id="contadorSombra"></div>
                <input type="range"  id="rangeSombra"/>
            </div>
            <div className="ranges" style={{border:"2px solid rgb(31, 29, 29)",padding: "20px 25px",width: "250px",boxSizing: "border-box",color:"white"}}>
                <div className="contador" id="contadorLinea"></div>
                <input type="range"  id="rangeLinea"/>
            </div>
        </div>
        <canvas id="canvas" width="500" height="500">Tu navegador no soporta canvas de HTML.</canvas>
        <div className="paletaColores">
            <div className="color" title="---" id="black" style={{background: "rgb(0, 0, 0)"}}></div>
            <div className="color" title="Blanco" id="white" style={{background: "rgb(255, 255, 255)"}}></div>
            <div className="color" title="Gris" id="grey" style={{background: "grey"}}></div>
            <div className="color" title="Rosado" id="pink" style={{background: "pink"}}></div>
            <div className="color" title="Agua" id="aqua" style={{background: "aqua"}}></div>
            <div className="color" title="Salmn" id="salmon" style={{background: "salmon"}}></div>
            <div className="color" title="Violeta" id="violet" style={{background: "violet"}}></div>
            <div className="color" title="Tomate" id="tomato" style={{background: "tomato"}}></div>
            <div className="color" title="---" id="springgreen" style={{background:"springgreen"}}></div>
            <div className="color" title="Amarillo" id="yellow" style={{background: "yellow"}}></div>
            <div className="color" title="---" id="olivedrab" style={{background: "olivedrab"}}></div>
            <div className="color" title="Chocolate" id="chocolate" style={{background:"chocolate"}}></div>
            <div className="color" title="Verde" id="green" style={{background: "green"}}></div>
            <div className="color" title="Rojo" id="red" style={{background: "red"}}></div>
            <div className="color" title="---" id="crimson" style={{background: "crimson"}}></div>
            <div className="color" title="Marrn" id="brown" style={{background: "brown"}}></div>
            <div className="color" title="Marrn oscuro" id="maroon" style={{background: "maroon"}}></div>
            <div className="color" title="---" id="slateblue" style={{background: "slateblue"}}></div>
            <div className="color" title="Purpura" id="purple" style={{background: "purple"}}></div>
            <div className="color" title="Azul" id="blue" style={{background: "blue"}}></div>
        </div>
    </div>
    )
}

export default Pizarra;