import { useEffect, useState } from 'react';
import { FaTrashAlt } from "react-icons/fa";

import CantidadProducto from './CantidadProducto';
import './productoLista.css';

const ProductoLista = ({objeto,evento,aumentar,disminuir,sumaTotal,aumentarMonto}) => {
    let [datos,setDatos] = useState({});

    const actualizarDatos = () => {
        setDatos(objeto);
    }
    useEffect(()=>{
        actualizarDatos();
    },[objeto])

    return (
        <div className="contenedor-producto-lista">
            <table >
                <thead >
                    <tr >
                        <th>Articulo</th>
                        <th>Cantidad</th>
                        <th>Precio</th>
                        <th id='thBtn' rowSpan={2} onClick={()=>{evento(objeto)}} title="Eliminar producto">
                            <FaTrashAlt/>
                        </th>
                    </tr>
                </thead>
                <tbody >
                    <tr>
                        <td>{datos.name}</td>
                        <td>{<CantidadProducto producto={objeto} aumentar={aumentar} cantidad={objeto.cantidad} disminuir={disminuir} aumentarMonto={()=>aumentarMonto}/>}</td>
                        <td>${objeto.price * objeto.cantidad}</td>
                    </tr>
                </tbody>
            </table>
        </div>
    )
}
export default ProductoLista;