import './modalAddLineChart.css';

const ModalAddLineChart = ({setShowModal,addTag,functionEmptyAndFilling}) => {
    let newTag = {};
    let data = [];
    let label,monday,tuesday,wednesday,thursday,friday,saturday,sunday,color;

    const creatingData = (e) => {
        switch(e.target.name){
            case "lable":
                label = e.target.value;
                break;
            case "monday":
                monday = parseFloat(e.target.value);
                break;
            case "tuesday":
                tuesday = parseFloat(e.target.value);
                break;
            case "wednesday":
                wednesday = parseFloat(e.target.value);
                break;
            case "thursday":
                thursday = parseFloat(e.target.value);
                break;
            case "friday":
                friday = parseFloat(e.target.value);
                break;
            case "saturday":
                saturday = parseFloat(e.target.value);
                break;
            case "sunday":
                sunday = parseFloat(e.target.value);
                break;
        }
    }
    const updateColor = (e) => {
        color = (e.target.value);
    }
    const addDataNewTag = () => {
        data = [monday,tuesday,wednesday,thursday,friday,saturday,sunday];
        newTag.label = label;
        newTag.backgroundColor = color;
        newTag.data = data;
    }
    
    const updateData = (e) =>{
        e.preventDefault();
        functionEmptyAndFilling();
        setShowModal()
    }

    return(
        <div className="container-modal-LineChart">
                <p>Fill out the fields</p>
                <form onSubmit={(e)=>updateData(e)} >
                <input type="text" placeholder="Lable*" name="lable" onChange={(e)=>creatingData(e)} required />
                <input type="number" placeholder="Value - Monday*" name="monday" onChange={(e)=>creatingData(e)} required />
                <input type="number" placeholder="Value - Tuesday*" name="tuesday" onChange={(e)=>creatingData(e)} required />
                <input type="number" placeholder="Value - Wednesday*" name="wednesday" onChange={(e)=>creatingData(e)} required />
                <input type="number" placeholder="Value - Thursday*" name="thursday" onChange={(e)=>creatingData(e)} required />
                <input type="number" placeholder="Value - Friday*" name="friday" onChange={(e)=>creatingData(e)} required />
                <input type="number" placeholder="Value - Saturday*" name="saturday" onChange={(e)=>creatingData(e)} required />
                <input type="number" placeholder="Value - Sunday*" name="sunday" onChange={(e)=>creatingData(e)} required />
                <input type="color" onChange={(e)=>updateColor(e)} />
                <input type="submit" value="Add" onClick={()=>{addDataNewTag();addTag(newTag)}} />
            </form>
        </div>
    )
}
export default ModalAddLineChart;