import React, { useState } from 'react';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Pie } from 'react-chartjs-2';
import ModalAddPieChart from './Modal-addPieChart';
import { FaPlus } from 'react-icons/fa';
import './pieChart.css';

ChartJS.register(ArcElement, Tooltip, Legend);

function PieChart({data,addTag}) {
  const [showModal,setShowModal] = useState(false)
  const [dataPie, setDataPie] = useState(data);

  const functionEmptyAndFilling = () =>{
    setDataPie({
      "labels":[],
      "datasets":[{
        "data":[]
      }]
    });
    setTimeout(() => {
      setDataPie(data);
    }, 0);
  }
  const updateModal = () => {
    if(showModal){
      setShowModal(false)
    }else{
      setShowModal(true)
    }
  }
  return (
    <div className="items">
        <FaPlus className="btn-addTag" title='Add label' onClick={()=>updateModal()} />
        <Pie className="graphPie" data={dataPie} />
        {showModal ? <ModalAddPieChart setShowModal={setShowModal} addTag={addTag} functionEmptyAndFilling={functionEmptyAndFilling} /> : null}
    </div>
  ); 
}
export default PieChart;


