import './cantidadProducto.css';
const CantidadProducto = ({producto = undefined,cantidad = undefined,aumentar,disminuir}) => {
    return (
        <span className='btns-cantidad'>
            <button className="boton" onClick={producto ? ()=>disminuir(producto) : disminuir()} >-</button>
            <b>{cantidad || producto.cantidad}</b>
            <button className="boton" onClick={producto ? ()=>aumentar(producto) : aumentar()} >+</button>
        </span>
    )
}
export default CantidadProducto;