import {Chart as ChartJS, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend, Filler} from 'chart.js';
import { useState } from 'react';
import { Line } from 'react-chartjs-2';
import {FaPlus} from 'react-icons/fa';
import ModalAddLineChart from './Modal-addLineChart';
ChartJS.register( CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend, Filler);

const scores = [132,63,233,440,234,65,345];
const labels = ["Monday","Tuesday","Wednesday","Thursday","Friday","Saturday","Sunday"];

const options = {
    "responsive" : true,
    "fill": true,
    "tension":.35,
    "pointRadius":0,
    "scales": {
        "y": {
            "beginAtZero": true
        }
    }
}

const LineChart = ({data,addTag}) => {
    const [showModal,setShowModal] = useState(false);
    const [dataLine, setDataLine] = useState(data.data);

    const functionEmptyAndFilling = () =>{
        setDataLine({
          "labels":[],
          "datasets":[]
        });
        setTimeout(() => {
          setDataLine(data.data);
        }, 0);
      }

    const updateModal = () => {
        if(showModal){
          setShowModal(false)
        }else{
          setShowModal(true)
        }
      }

    const data1 = {
        datasets: [
            // {
            //     // label: "mis datos",
            //     // data: scores,
            //     // tension: .3,
            //     "label":"Current Week Sales",
            //     "data":[54,352,120,204,86,294,73],
            //     "backgroundColor": "#31d497d1",
            //     "borderColor":"#31d497",
            //     "borderWidth":".7"
            // },
            {
                "label":"Current Week Sales",
                "data":[54,352,120,204,86,294,73],
                "backgroundColor": "#31d497d1",
                "borderColor":"#31d497",
                "borderWidth":".7"
            },
            {
                "label":"Last Week Sales",
                "data":[38,72,445,139,282,514,108],
                "backgroundColor":"#5d7f4e",
                "borderColor":"#000",
                "borderWidth":".7"
            }
        ],
        labels
    }
    return(
        <div className='items'>
            <FaPlus className="btn-addTag"  onClick={()=>updateModal()}/>
            <Line data={dataLine} options={data.options} style={{"width": "469px","maxHeight": "200px"}} />
            {showModal ? <ModalAddLineChart setShowModal={setShowModal} addTag={addTag} functionEmptyAndFilling={functionEmptyAndFilling} /> : null}
        </div>
    )
}

export default LineChart;































