import React, { useEffect, useState } from "react";
import MsgSaludo from "./Components/MsgSaludo";
import Producto from "./Components/Producto";
import ConfirmarProducto from "./Components/ConfirmarProducto";
import BtnLista from "./Components/BtnLista";
import Lista from "./Components/Lista";
import json from './json/db.json';
import CargandoProductos from "./Components/CargandoProductos";
import './tienda-online.css';
import imgFondoTienda from './images/fondo.jpg'

	let jsonData = JSON.stringify(json);
	localStorage.setItem("data",jsonData);
	let jsonSerializado = localStorage.getItem("data");
	let data = JSON.parse(jsonSerializado);

function TiendaOnline() {
	let [usuario,setUsuario] = useState("Extrao");
	let [saludar,setSaludar] = useState(false);
	let [btnLista,setBtnLista] = useState(false);
	let [mostrarConfirmacionProducto,setMostrarConfirmacionProducto] = useState(false);

	let [productoEnCurso,setProductoEnCurso] = useState(null);
	let [listaDeProductos,setListaDeProductos] = useState([]);

	let [cantidad,setCantidad] = useState(1);

const aumentarMontoTotal = (e) => { // esta funcion debe sumarle 1 a el objeto de que contiene todos los productos de la lista especificamente a la cantidad de el listaDeProductos en el que se seleccione el boton de sumar.

	let array = listaDeProductos;
	let array1 = [];

	array.map(elemento=>{
		if(e.id === elemento.id){
			e.cantidad = e.cantidad + 1;
			array1.push(e)
		}else{
			array1.push(elemento);
		}
	})
	setListaDeProductos(array1);
}

const disminuirMontoTotal = (e) => { 

	let array = listaDeProductos;
	let array1 = [];

	array.map(elemento=>{
		if(e.id === elemento.id){
			if(e.cantidad > 1){
				e.cantidad = e.cantidad - 1;
				array1.push(e)
			}else{
				array1.push(e)
			}
		}else{
			array1.push(elemento);
		}
	})
	setListaDeProductos(array1);
}


    const aumentarCantidad = () =>{
        setCantidad(cantidad + 1);
    }
    const disminuirCantidad = () =>{
        if(cantidad > 1) setCantidad(cantidad - 1)
    }



	const ocultarSaludo = () => {
		if (saludar) setSaludar(false)
	}

	const validarBtnLista = () =>{
		if (btnLista) setBtnLista(false)
		else setBtnLista(true)
	}

	const mostrarConfirmarProducto = (producto) => {
		setMostrarConfirmacionProducto(true);
		setProductoEnCurso(producto);
	}

	const cancelarConfirmarProducto = () => {
		setMostrarConfirmacionProducto(false)
		setProductoEnCurso();
		setCantidad(1);
	}


const aceptarConfirmarProducto = () => {
	productoEnCurso.cantidad = cantidad;
	let array = listaDeProductos;
	let arrayNuevo = [];
	let boolean = false;

	if(array.length > 0){
		array.forEach(e => {
			if(e.id === productoEnCurso.id){
				boolean = true ;
			}
		})
		
		if(boolean){
			array.map(e => {
				if(e.id !== productoEnCurso.id){
					arrayNuevo.push(e)
				}else{arrayNuevo.push(productoEnCurso)}
			})
			setListaDeProductos(arrayNuevo)
		}else{
			setListaDeProductos([...array,productoEnCurso])
		}
		
	}else{
		setListaDeProductos([...array,productoEnCurso])
	}

	setMostrarConfirmacionProducto(false);
	setCantidad(1);
}

	const eliminarProducto = (e) => {
		let nuevoArray = [];
		listaDeProductos.filter(elemento => {
			if(elemento.id !== e.id){
				nuevoArray.push(elemento)
			}
			setListaDeProductos(nuevoArray)
		})
	}

	useEffect(()=>{
	  let usuario = prompt("Introduce tu nombre aqui:");
	  setUsuario(usuario);
	  setSaludar(true);
	},[])
	return (
		<div className="contenedor-principal">
			<h1 className="titulo-tienda">Tienda Online</h1>
			<div className="contenedor-productos">
				{data.elementos.length <= 0 ? <CargandoProductos/> : data.elementos.map((e,i)=> <Producto elemento={e} key={i} evento={mostrarConfirmarProducto}/>)}
			</div> 
			<BtnLista evento={()=>validarBtnLista}/>
			{btnLista ? <Lista producto={listaDeProductos} ocultar={validarBtnLista} eliminar={eliminarProducto} aumentar={aumentarMontoTotal} disminuir={disminuirMontoTotal}/> : null}
			{mostrarConfirmacionProducto ? <ConfirmarProducto producto={productoEnCurso} rechazar={()=>cancelarConfirmarProducto} aceptar={()=>aceptarConfirmarProducto} cantidad={cantidad} aumentar={aumentarCantidad} disminuir={disminuirCantidad}/> : null}
			{saludar ? <MsgSaludo prop={usuario} evento={()=>ocultarSaludo}/> : null}
		</div>
	);
}
export default TiendaOnline;