import { useState } from "react";
import './modalAddPieChart.css';

const ModalAddPieChart = ({setShowModal,addTag,functionEmptyAndFilling}) => {
    const [tag,setTag] = useState("");
    const [value,setValue] = useState();
    const [color,setColor] = useState();

    const updateColor = (e) => {
        setColor(e.target.value);
    }
    const updateTag = (e) => {
        setTag(e.target.value);
    }
    const updateValue = (e) => {
        setValue(parseFloat(e.target.value));
    }

    const updateData = (e) => {
        e.preventDefault();
        functionEmptyAndFilling();
        setShowModal();
    }

    return ( 
        <div className="container-modal-PieChart">
            <form onSubmit={(e)=>updateData(e)}>
                <p>Fill out the fields</p>
                <input type="text" placeholder="Tag*" onChange={updateTag} required/>
                <input type="number" placeholder="Value*" onChange={updateValue} required/>
                <input type="color" onChange={(e)=>updateColor(e)}/>
                <input type="submit" value="Add" onClick={()=>addTag({tag,value,color})} />
            </form>
        </div>
    )
}
export default ModalAddPieChart;