import './confirmarProducto.css';
import CantidadProducto from './CantidadProducto';
import { useState } from 'react';
let cantid = 1;
const ConfirmarProducto = ({rechazar,aceptar,producto,cantidad,aumentar,disminuir}) => {

    return (
        <div className='contenedor-confirmacion'>
            <p><b>Quieres aadir al carrito?</b></p>
            <table>
                <thead>
                    <tr>
                        <th>Articulo</th>
                        <th>Cantidad</th>
                        <th>Precio</th>
                    </tr>
                </thead>
                <tbody >
                    <tr>
                        <td>{producto.name}</td>
                        <td>{<CantidadProducto cantidad={cantidad} aumentar={()=>aumentar} disminuir={()=>disminuir} />}</td>
                        <td>${producto.price * cantidad}</td>
                    </tr>
                </tbody>
            </table>
            <div>
                <button onClick={rechazar()}>Cancelar</button>
                <button onClick={aceptar()}>Aadir</button>
            </div>
        </div>
    )
}
export default ConfirmarProducto;