import { useState } from 'react';
import './producto.css';

const Producto = ({elemento,evento}) => {
	let imagen = require(`../images/imagen${elemento.id}.jpg`);

	return (
		<>
		   <div className="cont-producto">
				<div className='cont-img'>
					<img src={imagen} alt={"Esta imagen contiene: " + elemento.name}/>
				</div>
				<div className='cont-texto'>
						<h3>{elemento.name}</h3>
						<p>Precio: <b style={{color:"#255325"}}>${elemento.price}</b></p>
						<button onClick={()=>evento(elemento)}>Aadir</button>
				</div>
		   </div>
		</>
	)
}
export default Producto;