import { useEffect, useState } from "react";
import './lista.css';
import ProductoLista from "./ProductoLista";
import { FaTimes } from 'react-icons/fa';

const Lista = ({producto,eliminar,ocultar,aumentar,disminuir}) => {
    let [productos,setProductos] = useState([]);
    let [montoTotal,setMontoTotal] = useState(0);

    const sumaTotal = () => {
        let suma = 0;
        productos.map(e => {
            suma += e.price * e.cantidad;
        })
        setMontoTotal( suma );
    }

    useEffect(()=>{
        setMontoTotal(0);
        sumaTotal();
    },[productos])

    useEffect(()=>{
        if(producto.length >= 0) setProductos(producto);
    },[producto]);

    return (
        <div className="contenedor-lista-carrito">
            <span className="span"><h3>Lista de compra:</h3>
                <span className="svg-close-list">
                    <FaTimes style={{"cursor":"pointer"}} onClick={()=>ocultar()}/>
                </span>
            </span>
            <div className="elemento">
                {productos.length > 0 ? productos.map((e,i)=><ProductoLista objeto={e} evento={eliminar} key={i} cantidad={producto.cantidad} aumentar={aumentar} disminuir={disminuir} sumaTotal={sumaTotal}/>) : <p style={{padding:"20px",fontSize:"18px"}}>No hay productos en el carrito.</p>}
                {productos.length > 0 ? <div className="totalAPagar">Total a pagar: <b>{`$${montoTotal}`}</b></div> : null}
            </div>
        </div>
    )
}
export default Lista;