import photo9 from './imagenes/img(9).jpg';
import photo10 from './imagenes/img(10).jpg';
import photo11 from './imagenes/img(11).jpg';
import photo12 from './imagenes/img(12).jpg';
import photo13 from './imagenes/img(13).jpg';
import photo14 from './imagenes/img(14).jpg';
