import photo23 from './imagenes/img(23).jpg';

const Galeria = () =>{
 useEffect(()=>{
  document.body.style.background = "radial-gradient(rgba(0, 0, 255, 0.329),rgba(21, 21, 31, 0.774))";

  var mostrador = document.getElementById("mostrador");
  mostrador.style.display = "none";
  var img = document.createElement("img");
  img.setAttribute("class", "imgmost");
  mostrador.appendChild(img);
  img.addEventListener("click", (e)=>{addImgAndClass(e)})
  /*
  1 seleccionando el div mostrador
  2 creando una imagen
  3 agregandole la clase imgmost a la imagen
  4 agregandole la imagen a el div mostrador
  5 agregandole a la imagen el evento click
  */
  let btnX= document.createElement("p");
  let textoBtn = document.createTextNode("X");
  btnX.appendChild(textoBtn);
  mostrador.appendChild(btnX);
  btnX.addEventListener("click",()=>removeImagenAndClass())
 
  // img.appendChild(btnX);
  // document.body.appendChild(btnX);
  //aqui tengo el span que contiene la x;  debo crear un contenedor dentro del contenedor "mostrador" y dentro de 
 
  var imgdeprueba = document.querySelectorAll(".imgdeprueba");
  let numero = 0;
  while(numero < imgdeprueba.length){
   imgdeprueba[numero].addEventListener("click", (e)=>{addImgAndClass(e)});
   numero++;
  }

  function disableScroll(){  
    var x = window.scrollX;
    var y = window.scrollY;
    window.onscroll = function(){ window.scrollTo(x, y) };
  }

  function enableScroll(){  
      window.onscroll = null;
  }


  function addImgAndClass(e){
  img.setAttribute("src", e.target.src);
  // img.appendChild(btnX);
 disableScroll()
   if (mostrador.style.display === "none") {
     document.body.style.overflow = "hidden";
    mostrador.style.display = "block";
   mostrador.style.animation = "lentoC .3s forwards";
   }
  }
 
  function removeImagenAndClass(){
    enableScroll()
    img.removeAttribute("src");
    document.body.style.overflow = "auto";

   if (mostrador.style.display === "block") {
      mostrador.style.animation = "lentoQ .3s forwards";
    setTimeout(()=>{mostrador.style.display = "none";
     if (mostrador.style.display === "block") {
     }else{
     img.removeAttribute("src");}
    }, 300)
   }
  }
 },[])
	return (
  <>
   <div id="mostrador" className="mostrador-no"></div>
   <div className="contenedor-grid">
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo1} title="Imagen-3D"/>
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo2} title="Paisaje-Agua"/>
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo3} title="Autopista"/>
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo4} title="Paisaje-Agua"/>
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo5} title="Paisaje-Agua"/>
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo6} title="Paisaje-Playa"/>
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo7} title="Paisaje-Playa"/>
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo8} title="Paisaje-Ciudad-Playa"/>
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo9} title="Paisaje-Roca"/>
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo10} title="Paisaje-Agua"/>
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo11} title="Paisaje-Arquitectura"/>
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo12} title="Paisaje-Playa"/>
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo13} title="Paisaje-Agua"/>
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo14} title="Paisaje-Agua"/>
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo15} title="Paisaje-Ciudad"/>
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo16} title="Paisaje-Agua"/>
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo17} title="Paisaje-Ciudad"/>
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo18} title="Paisaje-Agua"/>
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo19} title="Paisaje-Agua"/>
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo20} title="Paisaje-Agua"/>
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo21} title="Paisaje-Ciudad"/>
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo22} title="Paisaje-Agua"/>
    <img className="imgs imgdeprueba" alt="Img-Wallpaper" src={photo23} title="Auto"/>
   </div>
  </>
	)
}
export default Galeria;