import { useEffect } from 'react';
import './home.css';
import imgFondo from '../Images/fondo.jpg';

const Home = () =>{
    useEffect(()=>{
        let body = document.body.style;
        body.backgroundImage = `url(${imgFondo})`;
        body.backgroundRepeat = "no-repeat";
        body.backgroundSize = "cover";
        body.backgroundAttachment = "fixed";
    },[])
    return (
        <>
            <div className='info-home'>
                <h1>Hi, I'm Keivy. I design and build user interfaces. </h1>
            </div>
        </>
    )
}
export default Home